export const deployment = `apiVersion: apps/v1
kind: Deployment
metadata:
  name: howdy-deployment
  labels:
    app: howdy-deployment
spec:
  replicas: 2
  selector:
    matchLabels:
      app: howdy-deployment
  template:
    metadata:
      labels:
        app: howdy-deployment
    spec:
      containers:
        - name: howdy
          image: jimetevenard/howdy:latest
          ports:
          - containerPort: 8080`;

export const ingress = `apiVersion: networking.k8s.io/v1
kind: Ingress
metadata:
  name: test-ingress
spec: 
  rules:
  - host: \$\{\{HOST\}\}
    http:
      paths:
      - path: /
        pathType: Prefix
        backend:
          service:
            name: mon-petit-service
            port:
              number: 80`;

export const service = `apiVersion: v1
kind: Service
metadata:
  name: mon-petit-service
spec:
  selector:
    app: howdy-deployment
  ports:
    - protocol: TCP
      port: 80
      targetPort: 8080`;

